.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}



.name-container {
    font-size: 36px;
    white-space: nowrap;
    overflow: hidden;
}

.name-container span {
    animation: slide 12s steps(10) infinite;
}

@keyframes slide {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

/* ... Name  styles Start... */

.name-container {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 5rem;
    font-weight: bolder;
}

.name-container span {
    opacity: 0;
    animation: show-letter 8s linear infinite;
}

.name-container span:nth-child(1) {
    animation-delay: -2s;
}

.name-container span:nth-child(2) {
    animation-delay: -1.8s;
}

.name-container span:nth-child(3) {
    animation-delay: -1.6s;
}

.name-container span:nth-child(4) {
    animation-delay: -1.4s;
}

.name-container span:nth-child(5) {
    animation-delay: -1.2s;
}

.name-container span:nth-child(6) {
    animation-delay: -1s;
}

.name-container span:nth-child(7) {
    animation-delay: -0.8s;
}

.name-container span:nth-child(8) {
    animation-delay: -0.6s;
}

.name-container span:nth-child(9) {
    animation-delay: -0.4s;
}

@keyframes show-letter {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    10% {
        opacity: 1;
        transform: translateX(0);
    }

    20% {
        opacity: 1;
        transform: translateX(0);
    }

    100% {
        opacity: 0;
        transform: translateX(-100px);
    }
}

/* ...  Name styles End ... */




.content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px) {

    .hero .content h1 {
        font-size: 3rem;

    }

    .hero .content p {
        font-size: 1.4rem;

    }
}