.project-heading {
    text-align: center;
    padding: 4rem 0 2rem;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card {
    background: black;
    padding: 1.2rem 1rem;
}

.project-card img {
    width: 100%;
}

.project-container :hover {
    background-color: #1a1919;
}

.project-title {
    padding: 1rem 0;
}

.project-details {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

.pro-btns {

    display: flex;
    justify-content: center;
    /* Center the button horizontally */
    align-items: center;
    /* Center the button vertically */
    height: 100%;
    /* Make sure the button fills the entire height of the parent */
    margin-top: 10px;
}

.pro-btns .btn {
    padding: 0.5rem 1rem;
    border-radius: 4px;
    /* Add some rounded corners */

}



@media screen and (max-width:740px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }

}